<script setup>
import Button from 'primevue/button'
import InputText from 'primevue/inputtext'
import InputNumber from 'primevue/inputnumber'

import draggable from "vuedraggable"
import Drager from 'es-drager'
import 'es-drager/lib/style.css'
import { cloneDeep, debounce } from 'lodash'
import { useRouter } from 'vue-router'
import { ref, computed, watch, onMounted, onUnmounted} from 'vue'


import MaterialContainer from "@/components/MaterialContainer.vue"
import DragItem from '@/components/DragItem.vue'
import Title from "@/components/Title.vue"


const imgNum = ref(4)

const leftBtn = ref('material')

const basicItem = {
    name: '', 
    url: '', 
    rotatable: true, 
    width: 100, 
    height: 100, 
    top:0, 
    left: 0, 
    equalProportion: true, 
    border: false,
    w_pos: 0.3,
    w_neg: 1.0,
    t_i: 0.3
}
const itemList = ref([])

const router = useRouter()
const projectInfo = ref({
    width: 500,
    height: 200,
    name: '',
    style: new Set(['']),
    type: new Set([''])
})

const materialTypes = ref({
    '': [], 
    '': [],
    '': [], 
    'LOGO': [], 
    '': []
})

onMounted(async ()=>{
    // 
    projectInfo.value.scale = 1
    projectInfo.value.width = router.currentRoute.value.query.width || 500
    projectInfo.value.height = router.currentRoute.value.query.height || 200
    projectInfo.value.name = router.currentRoute.value.query.name || ''
    record.snapshots.push([])
    record.curIndex++
    watch(itemList, debounce(recordPush, 50), {deep: true})

    // 
    window.addEventListener('keyup', handleDeleteMaterial)
})

onUnmounted(()=>{
    window.removeEventListener('keyup', handleDeleteMaterial)
})

const canvas_scale = ref(1)
const canvas = ref() // 
const canvas_outerbox = ref() //
// ctrl+
const handleZoom = (e) => {
    const modifyOuter = (type) => {
        let outerHeight = canvas_outerbox.value.offsetHeight
        let outerWidth = canvas_outerbox.value.offsetWidth
        let innerHeight = canvas.value.offsetHeight*canvas_scale.value
        let innerWidth = canvas.value.offsetWidth*canvas_scale.value
        if (type == 'zoomin') {
            canvas_outerbox.value.style.width = `${Math.max(innerWidth + 64, outerWidth)}px`
            canvas_outerbox.value.style.height = `${Math.max(innerHeight + 64, outerHeight)}px`
        }else {
            canvas_outerbox.value.style.width = `${Math.min(innerWidth, outerWidth)}px`
            canvas_outerbox.value.style.height = `${Math.min(innerHeight, outerHeight)}px`
        }
    }
    if (canvas_scale.value <= 0) return

    // ctrl
    if(e.ctrlKey) {
        // 
        e.preventDefault()
        // 
        if (e.deltaY < 0) {
          // 
          canvas_scale.value += 0.008
          modifyOuter('zoomin')
        } else {
          // 
          canvas_scale.value -= 0.008
          modifyOuter('zoomout')
        }
    }
}

// 
const handleDrop = (e) => {
    let item = e.dataTransfer.getData("item") || null
    if (!item) return
    item = JSON.parse(item)
    const rectObj = canvas.value.getBoundingClientRect()
    itemList.value.push({...basicItem, ...item, ...{left: e.clientX - rectObj.left - item.width*0.5, top: e.clientY - rectObj.top - item.height*0.5, ref: null, id: Math.round(Math.random()*10000)}})
}

const handleDragover = (e) => {
    e.preventDefault()
}

// index
const selectedItemIndex = ref(-1)
const selected = ref('hidden')
// 
const w_pos = computed({
    get: () => {
        if(selected.value !== 'hidden' && selectedItemIndex.value < itemList.value.length && selectedItemIndex.value >= 0)
        {
            return itemList.value[selectedItemIndex.value].w_pos
        }
        else return 0
    },
    set: (val) => {
        if (selectedItemIndex.value == -1) return
        val = parseFloat(val.toFixed(1))
        if (val >= 1 || val <= 0) return
        itemList.value[selectedItemIndex.value].w_pos = val
    }
})
const w_neg = computed({
    get: () => {
        if(selected.value !== 'hidden' && selectedItemIndex.value < itemList.value.length && selectedItemIndex.value >= 0) 
        {
            return itemList.value[selectedItemIndex.value].w_neg
        }
        else return 0
    },
    set: (val) => {
        if (selectedItemIndex.value == -1) return
        val = parseFloat(val.toFixed(1))
        if (val >= 3 || val <= 0) return
        itemList.value[selectedItemIndex.value].w_neg = val
    }
})
const t_i = computed({
    get: () => {
        if(selected.value != 'hidden' && selectedItemIndex.value < itemList.value.length && selectedItemIndex.value >= 0) 
        {
            return itemList.value[selectedItemIndex.value].t_i
        }
        else return 0
    },
    set: (val) => {
        if (selectedItemIndex.value == -1) return
        val = parseFloat(val.toFixed(1))
        if (val > 1 || val < 0) return
        itemList.value[selectedItemIndex.value].t_i = val
    }
})

const saveSuccess = ref('hidden')
// 
const handleSave = async () => {
    saveSuccess.value = 'visible'
    setTimeout(() => {
        saveSuccess.value = 'hidden'
    }, 5000);
}
// 
const handleGenerate = async () => {
    await handleSave()
    alert('')
}

// itemList
// drag
let drag_evt = {
    left: 0,
    top: 0,
    item_left: 0,
    item_top: 0,
    item: null
}
const handleMouseDown = (e, item) => {
    // e.preventDefault()
    drag_evt.item_left = item.left
    drag_evt.item_top = item.top
    drag_evt.left = e.clientX
    drag_evt.top = e.clientY
    drag_evt.item = item
    window.addEventListener('mousemove', handleMouseMove)
    window.addEventListener('mouseup', handleMouseUp)
}
const handleMouseMove = (e) => {
    e.preventDefault()
    drag_evt.item.left = drag_evt.item_left + (e.clientX - drag_evt.left)/canvas_scale.value
    drag_evt.item.top = drag_evt.item_top + (e.clientY - drag_evt.top)/canvas_scale.value
}
const handleMouseUp = (e) => {
    e.preventDefault()
    drag_evt.item.left = drag_evt.item_left + (e.clientX - drag_evt.left)/canvas_scale.value
    drag_evt.item.top = drag_evt.item_top + (e.clientY - drag_evt.top)/canvas_scale.value
    window.removeEventListener('mousemove', handleMouseMove)
    window.removeEventListener('mouseup', handleMouseUp)
}
const handleClick = (e, index) => {
    selected.value = 'visible'
    selectedItemIndex.value = index
    let item = itemList.value[index]
    console.log(e.target);
    let item_svg = e.target.querySelector('.es-drager-rotate')
    if (item_svg) item_svg.style.display = 'block'
    // 
    const activeList = canvas.value.querySelectorAll('.selected')
    activeList.forEach(activeItem => {
        if (activeItem !== e.target) {
            activeItem.classList.remove('selected')
            let svg = activeItem.querySelector('.es-drager-rotate')
            if(svg) svg.style.display = 'none'
        }
    })
}
// 
const handleBlur = (e, index=null) => {
}
const handleResizeEnd = (e, item) => {
    item.width = e.width
    item.height = e.height
}
const handleRotateEnd = (e, item) => {
    item.angle = e.angle
}

const handleDeleteMaterial = (e) => {
    if (e.target.tagName !== 'INPUT' && (e.keyCode == 46 || e.keyCode == 8)) {
        if (selectedItemIndex.value !== -1) {
            itemList.value.splice(selectedItemIndex.value, 1)
            selectedItemIndex.value = -1
        }
    }
}

// 
// 
const record = {
    snapshots: [],
    curIndex: 0,
    maxLimit: 20,
    isSnapshot: true
}

// 
const same = () => {
    if (itemList.value.length != record.snapshots[record.curIndex-1].length) return false
    for (let i = 0; i < itemList.value.length; ++i) {
        for (let key in itemList.value[i]) {
            if (key != 'ref' && itemList.value[i][key] !== record.snapshots[record.curIndex-1][i][key]) {
                return false
            }
        }
    }
    return true
}

const recordPush = () => {
    // 
    if (same()) {
        return
    }
    // 
    if (record.curIndex == record.maxLimit) {
        record.snapshots.shift()
        curIndex--
    }
    // 
    if (record.curIndex != record.snapshots.length) {
        record.snapshots.splice(record.curIndex, record.snapshots.length)
    }
    record.snapshots.push(cloneDeep(itemList.value))
    record.curIndex++
}


const handleUndo = () => {
    if (record.curIndex == 1) return
    record.isSnapshot = false
    record.curIndex = record.curIndex-1
    itemList.value = cloneDeep(record.snapshots[record.curIndex-1])
}

const handleRedo = () => {
    if (record.curIndex >= record.snapshots.length) return
    record.isSnapshot = false
    record.curIndex = record.curIndex+1
    itemList.value = cloneDeep(record.snapshots[record.curIndex-1])
}
</script>

<template>
<main class="flex flex-col h-screen w-screen divide-y overflow-hidden">
    <!-- header -->
    <div class="flex flex-row grow-0 h-fit px-9 py-4 justify-between align-middle text-center">
        <div class="flex flex-row justify-between flex-1">
            <div class="flex flex-row gap-4">
                <svg width="48" height="45" viewBox="0 0 48 45" fill="none" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
                    <rect width="48" height="45" rx="10" fill="url(#pattern0_359_84)"/>
                    <defs>
                    <pattern id="pattern0_359_84" patternContentUnits="objectBoundingBox" width="1" height="1">
                    <use xlink:href="#image0_359_84" transform="matrix(0.000694959 0 0 0.00074129 -0.00315048 0)"/>
                    </pattern>
                    <image id="image0_359_84" width="1448" height="1349" xlink:href="data:image/png;base64,"/>
                    </defs>
                </svg>
                <router-link to="/" id="BannerText">
                    <h2 class="text-3xl mx-1">
                        <Title/>
                    </h2>
                </router-link>
            </div>
            <!--  -->
            <div class="flex flex-row gap-[18px] justify-between">
                <!--  -->
                <Button @click="handleUndo">
                    <svg  class="icon w-6 h-6" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M15.13 19.0601H7.13C6.72 19.0601 6.38 18.7201 6.38 18.3101C6.38 17.9001 6.72 17.5601 7.13 17.5601H15.13C17.47 17.5601 19.38 15.6501 19.38 13.3101C19.38 10.9701 17.47 9.06006 15.13 9.06006H4.13C3.72 9.06006 3.38 8.72006 3.38 8.31006C3.38 7.90006 3.72 7.56006 4.13 7.56006H15.13C18.3 7.56006 20.88 10.1401 20.88 13.3101C20.88 16.4801 18.3 19.0601 15.13 19.0601Z"/>
                        <path d="M6.43 11.5599C6.24 11.5599 6.05 11.4899 5.9 11.3399L3.34 8.77988C3.05 8.48988 3.05 8.00988 3.34 7.71988L5.9 5.15988C6.19 4.86988 6.67 4.86988 6.96 5.15988C7.25 5.44988 7.25 5.92988 6.96 6.21988L4.93 8.24988L6.96 10.2799C7.25 10.5699 7.25 11.0499 6.96 11.3399C6.82 11.4899 6.62 11.5599 6.43 11.5599Z"/>
                    </svg>
                </Button>
                <!--  -->
                <Button @click="handleRedo">
                    <svg class="icon w-6 h-6" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M16.87 19.0601H8.87C5.7 19.0601 3.12 16.4801 3.12 13.3101C3.12 10.1401 5.7 7.56006 8.87 7.56006H19.87C20.28 7.56006 20.62 7.90006 20.62 8.31006C20.62 8.72006 20.28 9.06006 19.87 9.06006H8.87C6.53 9.06006 4.62 10.9701 4.62 13.3101C4.62 15.6501 6.53 17.5601 8.87 17.5601H16.87C17.28 17.5601 17.62 17.9001 17.62 18.3101C17.62 18.7201 17.29 19.0601 16.87 19.0601Z"/>
                        <path d="M17.57 11.5599C17.38 11.5599 17.19 11.4899 17.04 11.3399C16.75 11.0499 16.75 10.5699 17.04 10.2799L19.07 8.24988L17.04 6.21988C16.75 5.92988 16.75 5.44988 17.04 5.15988C17.33 4.86988 17.81 4.86988 18.1 5.15988L20.66 7.71988C20.95 8.00988 20.95 8.48988 20.66 8.77988L18.1 11.3399C17.95 11.4899 17.76 11.5599 17.57 11.5599Z"/>
                    </svg>
                </Button>
            </div>
        </div>
        <!--  -->
        <div class="flex flex-row flex-1 justify-center align-center">
            <InputText class="text-[#5f5f5f] text-center" type="text" v-model="projectInfo.name"/>
        </div>
        <div class="flex flex-row flex-1 justify-between">
            <!--  -->
            <div class="flex flex-row justify-between">
                <div class="flex flex-row items-center mr-[1rem]" :style="{visibility: saveSuccess}">
                    <svg class="h-[1.128rem] mr-[0.35rem]" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M7.75002 19H3.25C2.00735 19 0.999992 17.9926 1 16.75L1.00009 3.24999C1.00009 2.00736 2.00745 1 3.25009 1H13.3754C14.618 1 15.6254 2.00736 15.6254 3.25001V9.43752M11.1254 15.8125L13.1879 17.875L18.4379 12.25" stroke="#5F5F5F" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                    </svg>
                    <span class="inline-block text-xs text-[#5F5F5F]"></span>
                </div>
                <Button @click="handleSave" class="py-2 px-[1.6rem] border border-[#eeaa1e] hover:bg-[#eeaa1e] rounded-md text-sm text-[#eeaa1e] hover:text-white font-normal"></Button>
            </div>
            <!--  -->
            <div class="flex flex-row">
                <div class="flex flex-row mr-[0.7rem] gap-[4px] justify-between items-center">
                    <Button class="w-8 h-8 rounded-md" @click="()=>{imgNum--}">
                        <svg width="32" height="34" viewBox="0 0 32 34" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <rect width="32" height="34" rx="5" fill="#F6F4F0"/>
                            <path d="M18.8047 22.416C19.0651 22.1493 19.0651 21.7168 18.8047 21.4501L14.6094 17.1518L18.8047 12.8535C19.0651 12.5867 19.0651 12.1543 18.8047 11.8875C18.5444 11.6208 18.1223 11.6208 17.862 11.8875L13.1953 16.6688C12.9349 16.9355 12.9349 17.368 13.1953 17.6347L17.862 22.416C18.1223 22.6828 18.5444 22.6828 18.8047 22.416Z" fill="#8B8B8B"/>
                        </svg>
                    </Button>
                    <span class="inline-block px-[1.6rem] h-[2.25rem] leading-[2.25rem] bg-[#eeaa1e] rounded-md text-sm text-[#ffffff] font-normal">{{imgNum}}</span>
                    <Button class="w-8 h-8 bg-[#eeaa1e] rounded-md text-sm text-[#ffffff] font-normal" @click="()=>{imgNum++}">
                        <svg width="32" height="34" viewBox="0 0 32 34" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <rect x="32" y="34" width="32" height="34" rx="5" transform="rotate(-180 32 34)" fill="#F6F4F0"/>
                            <path d="M13.1953 11.584C12.9349 11.8507 12.9349 12.2832 13.1953 12.5499L17.3906 16.8482L13.1953 21.1465C12.9349 21.4133 12.9349 21.8457 13.1953 22.1125C13.4556 22.3792 13.8777 22.3792 14.138 22.1125L18.8047 17.3312C19.0651 17.0645 19.0651 16.632 18.8047 16.3653L14.138 11.584C13.8777 11.3172 13.4556 11.3172 13.1953 11.584Z" fill="#8B8B8B"/>
                        </svg>
                    </Button>
                </div>
                <Button
                 @click="handleGenerate"
                 class="py-2 px-[1.6rem] bg-[#eeaa1e] rounded-md text-sm text-[#ffffff] font-normal">
                
                </Button>
            </div>

        </div>
    </div>
    <!-- main -->
    <div class="flex flex-row h-0 items-stretch divide-x grow">
        <!--  -->
        <div class="relative w-[4vw] px-[5px] py-[7px] flex flex-col justify-start gap-[10px] z-[100]">
            <div class="flex flex-col w-full items-center py-[11px] gap-[9px] fill-[#5F5F5F] stroke-white rounded-lg cursor-pointer text-[--c-grey]" :class="{selectedBtn: leftBtn=='material'}" @click="()=>{leftBtn='material'}">
                <svg class="w-[45%]" viewBox="0 0 25 23" xmlns="http://www.w3.org/2000/svg">
